.skyplus-offcanvas {
  &.is-hidden {
    .skyplus-offcanvas__overlay {
      opacity: 0;
      visibility: hidden;
    }

    .skyplus-offcanvas__contents {
      transform: translate(0, 0);
    }
  }

  &__overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    visibility: visible;
    opacity: 1;
    transition: all 0.25s ease-in;
    background-color: rgba(0, 0, 0, 0.5);
  }

  &__contents {
    padding: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 480px;
    height: 100%;
    overflow: auto;
    background-color: get-color(white);
    transition: all 0.25s ease-in-out;
    transform: translate(-100%, 0);
    display: flex;
    flex-direction: column;

    &--title {
      font-size: 24px;
    }

    &--header {
      height: rem(150px);
      display: flex;
      align-items: center;
      background-color: get-color(white);
      justify-content: center;

      @include media('>=tablet') {
        background-color: get-color(white);
        justify-content: flex-start;
      }

      &::after {
        margin-left: rem(20px);
        font-family: $font-family-indigo;
        content: '\e947' !important;
        color: get-color('white');
        cursor: pointer;

        @include media('>=tablet') {
          color: get-color(color-action-active);
        }
      }

      @include media('>=tablet') {
        height: rem(50px);
        box-shadow: 0px 0px 12px 0px #4c5d9e14;
      }
    }

    &--body {
      padding: 0 rem(20px) rem(80px);
      overflow: auto;
      background: linear-gradient(
        180deg,
        rgba(254, 254, 254, 0) 0%,
        rgba(255, 255, 255, 0.958959) 8.73%,
        #f7f9fe 17.51%,
        #ecf7fc 81.97%
      );
      flex: 1;
      overflow-x: hidden;
    }
    &--footer {
      display: flex;
      align-items: center;
      justify-content: center;
      height: rem(80px);
      background-color: get-color(white);
      border-top-left-radius: rem(16px);
      border-top-right-radius: rem(16px);
      bottom: 0;
      position: absolute;
      width: 100%;
    }
  }
}
