.skyplus-button {
  button {
    border-radius: rem(20px);
    padding: rem(8px) rem(32px);
    cursor: pointer;
    border: none;

    &:focus-visible,
    &:focus {
      outline: none;
    }
  }

  &--small {
    font-size: 14px;
    padding: 4px;
  }

  &--medium {
    font-size: rem(16px);
    line-height: 1.5;
    padding: rem(8px) rem(32px);
  }
  &--large {
    font-size: rem(20px);
    padding: rem(12px);
  }

  &--disabled {
    opacity: 0.2 !important;
    cursor: default !important;
  }

  &--outline {
    background-color: get-color(action-active-background);
  }

  &--outline-primary {
    color: get-color(color-primary-main);
    border: 1px solid get-color(primary-main) !important;
  }

  &--filled-primary {
    background-color: get-color(primary-main);
    color: get-color(action-active-background);
  }
  &--block {
    width: 100%;
    display: block;
  }
}
