$box-shadow: #4c5d9e14;
$white: #ffffff;
$indigo-info: #eaf8ff;
$secondary-main: #afe4ff;
$indigo-primary: #000099;
$text-primary: #25304b;
$text-tertiary: #7a85a0;

.skyplus-accordion {
  border-radius: rem(8px);
  box-shadow: 0 0 rem(12px) 0 $box-shadow;
  background: $white;
  margin-bottom: rem(12px);

  &__header {
    cursor: pointer;
    padding: rem(12px) rem(16px);

    &--active {
      background: $indigo-info;
      border-radius: rem(8px) rem(8px) 0 0;
    }

    &-right {
      width: 100%;
    }

    &-left {
      background: $secondary-main;
      width: rem(4px);
      border-radius: rem(4px);
      margin-right: rem(12px);

      &--active {
        background: $indigo-primary;
      }
    }

    &-container {
      display: flex;
      justify-content: space-between;
    }

    &-name {
      color: $text-primary;
      margin-bottom: rem(4px);
      font-size: rem(14px);
    }

    @include media('>=tablet') {
      padding: rem(16px) rem(24px) rem(16px) rem(24px);

      &-name {
        font-size: rem(16px);
      }
    }
  }

  &__head-root {
    display: flex;
  }

  &__subtitle {
    font-size: rem(12px);
    color: $text-tertiary;

    @include media('>=tablet') {
      font-size: rem(14px);
    }
  }

  &__footer {
    margin-top: rem(12px);
    border: rem(1.5px) dotted $secondary-main;
    padding: rem(12px);
    border-radius: rem(8px);

    &-text {
      font-size: rem(12px);
      color: $text-primary;
    }
  }

  &__body {
    padding: rem(12px) rem(16px);

    @include media('>=tablet') {
      padding: rem(16px) rem(24px);
    }
  }

  &__icon {
    transition: all 0.6s;
    &--isActive {
      transform: rotate(180deg);
    }
  }
}
