@mixin arrowButton() {
  cursor: pointer;
  height: rem(20px);
  width: rem(20px);
  top: rem(2.54px);
  left: (2.25px);
}

.skyplus-carousel {
  &__header {
    display: flex;
    padding-bottom: rem(16px);
    justify-content: space-between;
  }

  &__header-title {
    display: flex;
    font-size: rem(18px);
    font-family: 'Bauhaus Std';
    margin-left: 2%;

    @include media('>=tablet') {
      font-size: rem(24px);
    }
  }

  &__header-icon {
    display: flex;
    justify-content: space-between;
    padding: rem(0px) rem(20px) rem(0px) rem(20px);

    &--left {
      @include arrowButton();
    }

    &--right {
      @include arrowButton();
    }
  }

  &__slider-container {
    position: relative;
    width: 100%;
    overflow: hidden;
  }

  &__slider-wrapper {
    display: flex;
    transition: transform 1s ease;
    width: 100%;
  }

  &__slide {
    display: block;
    margin-left: auto;
    margin-right: auto;
    flex: 0 0 100%;
    width: 100%;
    border-radius: rem(4px);
    width: 100%;
    height: auto;

    @include media('>=tablet') {
      flex: 0 0 78%;
      margin-left: 2%;
      border-radius: rem(4px);
    }
  }

  &__dots-container {
    text-align: center;
  }

  &__dot {
    display: inline-block;
    height: rem(8px);
    width: rem(8px);
    margin: 0 rem(2px);
    border-color: #eae8e8;
    background-color: #e2ebf2;
    border-radius: 50%;
    cursor: pointer;

    &--active {
      width: rem(32px);
      height: rem(8px);
      border-radius: rem(6px);
      background-color: #000099;
    }
  }
}
