.flight-journey-tab-container {
  display: flex;
  gap: rem(5px);
  flex-direction: column;

  &__outer {
    border: var(--border-secondary);
    border-radius: rem(24px);
    padding: rem(4px);
  }

  &__leg {
    border-radius: rem(24px);
    background-color: get-color(primary-main);
    color: get-color(secondary-white);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: rem(8px) rem(16px);

    span {
      font-size: rem(16px);
      display: inline-block;
      font-weight: 500;

      &:nth-child(1) {
        display: flex;
        align-items: center;
        justify-content: center;
        &::after {
          content: '';
          border: rem(1px) dotted;
          width: rem(48px);
          display: inline-block;
          margin: 0 rem(20px);
          border-bottom: 0;
          border-left: 0;
          border-right: 0;
        }
      }
    }
  }
}
