// == Padding
$padding-1: var(--padding1, 10px);
$padding-13: var(--padding13, 13px);
$padding-15: var(--padding15, 15px);
$padding-16: var(--padding16, 16px);
$padding-2: var(--padding2, 20px);
$padding-4: var(--padding4, 40px);

// Normal mode
$color-foreground: #202020;
$color-background: get-color('light-yellow');
$color-link: #2020e0;

// Dark mode
$color-foreground-dark: $color-foreground;
$color-background-dark: $color-background;
$color-link-dark: $color-link;

