.skyplus-dropdown {
  &-element {
    border-radius: 12px;
    border: var(--border-secondary);
    line-height: 24px;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    padding: 10px 12px;
    cursor: pointer;
  }
  &-popover {
    border-radius: 12px;
    border: var(--border-secondary);
    box-shadow: 0px 0px 12px 0px #4c5d9e14;
    position: relative;

    .skyplus-dropdown-list__searchbox {
      position: sticky;
      top: 0;
      background-color: get-color(action-active-background);
      .search-inputbox {
        margin: 16px;
        padding: 10px 12px 10px 12px;
        font-size: 16px;
        border-radius: 25px;
        border: 1px dotted #afe4ff;

        &:focus-visible {
          outline: none;
        }
      }
      hr {
        border: none;
        border-top: 1px solid #daf1ff;
        width: 100%;
      }
    }

    .skyplus-dropdown-list--container {
      &__item {
        display: flex;
        padding: 12px 16px;
        gap: 8px;
        .icon {
        }
        .left {
          flex: 1;
        }
        .right {
        }
      }
    }
  }

  &-list__searchbox {
  }
}
